import React, { useState } from "react";
import style from "./index.module.scss";

import { useForm } from "react-hook-form";
import clsx from "clsx";
import { ClipLoader } from "react-spinners";

import WithPadding from "@components/WithPadding";
import { postContact } from "@apis/general";

import TopRightImg from "@assets/contact/top_right.svg";
import ContactUsImg from "@assets/contact/contact_us.svg";

export default function Contact() {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm({ mode: "onBlur" });

  function onSubmit(data) {
    setApiError(false);
    setIsLoading(true);
    postContact(data)
      .then((resp) => {
        setApiSuccess(true);
        reset();
      })
      .catch((err) => {
        console.log(err);
        setApiError(true);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }

  const [apiError, setApiError] = useState(false);
  const [apiSuccess, setApiSuccess] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  return (
    <div className={style.wrapper}>
      <WithPadding className={style.inner}>
        <div className={clsx(style.graphic, style.topRight)}>
          <img src={TopRightImg} alt="" />
        </div>
        <form onSubmit={handleSubmit(onSubmit)}>
          <input placeholder="Enter name of your child" {...register("child_name", { required: "* Required" })} />
          {errors.child_name && <p>{errors.child_name.message}</p>}

          <input
            placeholder="Email Address"
            {...register("email", {
              required: "* Required",
              pattern: {
                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                message: "invalid email address",
              },
            })}
          />
          {errors.email && <p>{errors.email.message}</p>}

          <input
            type="number"
            placeholder="Mobile Number"
            {...register("phone", {
              required: "* Required",
              maxLength: 11,
              minLength: 8,
            })}
          />
          {errors.phone && <p>Please enter valid phone number.</p>}

          <textarea {...register("message", { required: "* Required" })} placeholder="Message" />
          {errors.message && <p>{errors.message.message}</p>}

          {apiError && <p className={style.apiError}>An error occured. Please try again</p>}

          {apiSuccess && <p className={style.apiSuccess}>Your message was submitted. Thankyou for reaching out.</p>}

          <button disabled={isLoading} type="submit">
            {isLoading ? <ClipLoader size={20} color="#9f0b91" /> : "Submit"}
          </button>
        </form>
        <div className={style.image}>
          <header>
            <h1>We would be so happy to hear from you !</h1>
          </header>
          <img src={ContactUsImg} alt="" />
        </div>
      </WithPadding>
    </div>
  );
}
